





















































export default {
	data() {
		return {
			credit_id:'',
			card: [],
			loading:false,
		}
	},
	onLoad(e) {
		if(e.credit_id){
			this.credit_id = e.credit_id;
		}else{
			uni.navigateBack();
		}
	},
	onShow() {
		this.getCreditCardInfo();
	},
	onBackPress() {
		uni.switchTab({
			url: '/pages/tabBar/card/card'
		})
	},
	methods: {
		goDetailPage(url) {
			const openid = this.$store.state.openid;
			if (openid === null) {
				uni.navigateTo({
					url: '/pages/user/login/login'
				})
			} else {
				uni.navigateTo({
					url: url+'?credit_id='+this.credit_id
				})
			}
		
		},
		delCredit(){
			const that =this;
			const credit_id = this.credit_id;
			uni.showModal({
				content: "是否解绑该信用卡？",
				confirmText: "确定",
				cancelText: "取消",
				success:function(e){
					if(e.confirm){
						const openid = that.$store.state.openid;
						const sessionKey = that.$store.state.sessionKey;
						try {
							that.$http.post(that.websiteUrl+'/api/card/delCredit',{openid,sessionKey,credit_id},(res) => {
								if(res.data.code==1){
									uni.switchTab({
										url: '/pages/tabBar/card/card'
									})
								}else{
									uni.showModal({
										content:res.data.msg,
										showCancel:false
									})
								}
							});
						} catch (e) {
							uni.showModal({
								content:'网络异常,请稍后重试...',
								showCancel:false
							})
						}
					}
				}
			})
		},
		getCreditCardInfo() {
			const openid = this.$store.state.openid;
			const sessionKey = this.$store.state.sessionKey;
			const credit_id = this.credit_id;
			try {
				this.$http.post(this.websiteUrl + '/api/card/getCreditCardInfo', {
					openid,
					sessionKey,
					credit_id
				}, (res) => {
					if (res.data.code == 1) {
						this.card = res.data.result;
						uni.setNavigationBarTitle({
							title: this.card.bank_name,
						})
					}
				});
			} catch (e) {
				uni.showModal({
					content: '网络异常,请稍后重试...',
					showCancel: false
				})
			}
		},
	}
}
